import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // },
    {
      path: '/', //    登录
      component: resolve => require(['@/components/Login.vue'], resolve)
    },
    {
      path: '/login', //    登录
      component: resolve => require(['@/components/Login.vue'], resolve)
    },
    {
      path: '/usercenter', //    个人中心
      component: resolve => require(['@/components/UserCenter.vue'], resolve)
    },
    {
      path: '/serverList', //    服务费列表
      component: resolve => require(['@/components/ServerList.vue'], resolve)
    },
    {
      path: '/serverDetail', //    服务费详情
      component: resolve => require(['@/components/ServerDetail.vue'], resolve)
    },
    {
      path: '/bill', //    账单列表
      component: resolve => require(['@/components/Bill.vue'], resolve)
    },
    {
      path: '/billDetail', //    账单详情
      component: resolve => require(['@/components/BillDetail.vue'], resolve)
    },
    {
      path: '/openSMS', //    还款日短信提醒
      component: resolve => require(['@/components/OpenSMSAlerts.vue'], resolve)
    },
    {
      path: '/registered', //    注册，上传认证
      component: resolve => require(['@/components/Registered.vue'], resolve)
    },
    {
      path: '/registeredStep', //    认证流程结果
      component: resolve => require(['@/components/RegisteredStep.vue'], resolve)
    },
    {
      path: '/contractList', //    合同列表
      component: resolve => require(['@/components/ContractList.vue'], resolve)
    },
    {
      path: '/repayment', //    立即还款
      component: resolve => require(['@/components/Repayment.vue'], resolve)
    },
    {
      path: '/changePwd', //    修改密码
      component: resolve => require(['@/components/ChangePwd.vue'], resolve)
    }
  ]
})



